import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getProductsBySlug } from "../../../actions";
//import Card from "../../../components/UI/Card";
import { generatePublicUrl } from "../../../urlConfig";
//import { BiRupee } from "react-icons/bi";
import { Link } from "react-router-dom";

import "./style.css";

/**
 * @author
 * @function ClothingAndAccessories
 **/

const ClothingAndAccessories = (props) => {
  const product = useSelector((state) => state.product);
  const dispatch = useDispatch();

  useEffect(() => {
    const { match } = props;
    dispatch(getProductsBySlug(match.params.slug));
  }, []);

  return (
   
    <div className='container-fluid p-5'>
      <div className='row'>
        {product.products.map((product) => (
          <div className='col-lg-3 mt-5'>
            <div className='card-block product1-card p-3'>
              <Link

                to={`/${product.slug}/${product._id}/p`}
              >
                <img className='card-img product-img' src={generatePublicUrl(product.productPictures[0].img)} alt='card' />
              </Link>
              <div>
                <h4 className='text-center text-capitalize mt-3'>{product.name}</h4>
                <hr></hr>
                <h3 className='text-center mt-3' style={{ color: '#518432' }}>
                  Rs {product.price}
                </h3>
                <div className='text-center mt-3 mb-3'>
                  <a href='/Lucky-Bamboo/608edce7b1226643d76c5d7d/p' className='btn buy-btn '>Buy Now</a>
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default ClothingAndAccessories;
