import React, { useEffect, useState } from "react";
import "./style.css";
//import flipkartLogo from "../../images/logo/flipkart.png";
// import goldenStar from "../../images/logo/logo.jpeg";
//import { IoIosArrowDown, IoIosCart, IoIosSearch } from "react-icons/io";
import {
  Modal,
  MaterialInput,
  MaterialButton,
  DropdownMenu
} from "../MaterialUI";
import { useDispatch, useSelector } from "react-redux";
import { login, signout, getCartItems, signup as _signup } from "../../actions";
import Cart from "../UI/Cart";
import { Link } from 'react-router-dom'
/**
 * @author
 * @function Header
 **/

const Header = (props) => {
  const [loginModal, setLoginModal] = useState(false);
  const [signup, setSignup] = useState(false);
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  // const [error, setError] = useState("");
  const auth = useSelector((state) => state.auth);
  const dispatch = useDispatch();

  // state cart value
  const cart = useSelector((state) => state.cart);

  const userSignup = () => {
    const user = { firstName, lastName, email, password };
    if (
      firstName === "" ||
      lastName === "" ||
      email === "" ||
      password === ""
    ) {
      return;
    }

    dispatch(_signup(user));
  };

  const userLogin = () => {
    if (signup) {
      userSignup();
    } else {
      dispatch(login({ email, password }));
    }
  };

  const logout = () => {
    dispatch(signout());
  };

  useEffect(() => {
    if (auth.authenticate) {
      setLoginModal(false);
    }
  }, [auth.authenticate]);

  useEffect(() => {
    dispatch(getCartItems());
  }, []);

  const renderLoggedInMenu = () => {
    return (
      <DropdownMenu
        menu={<a className="fullName">{auth.user.fullName}</a>}
        menus={[
          // { label: "My Profile", href: "", icon: null },

          {
            label: "Orders",
            href: `/account/orders`,
            icon: null,
          },

          { label: "Logout", href: "", icon: null, onClick: logout },
        ]}
      />
    );
  };





  const renderNonLoggedInMenu = () => {
    return (
      <DropdownMenu
        menu={
          <a
            className="loginButton"
            onClick={() => {
              setSignup(false);
              setLoginModal(true);
            }}
          >
            Login
          </a>
        }
        menus={[
          { label: "My Profile", href: "", icon: null },

          {
            label: "Orders",
            href: `/account/orders`,
            icon: null,
            onClick: () => {
              !auth.authenticate && setLoginModal(true);
            },
          },

        ]}
        firstMenu={
          <div className="firstmenu">
            <span>New Customer?</span>
            <a
              onClick={() => {
                setLoginModal(true);
                setSignup(true);
              }}
              style={{ color: "#518432" }}
            >
              Sign Up
            </a>
          </div>
        }
      />
    );
  };

  return (
    <div className="header">

      <Modal visible={loginModal} onClose={() => setLoginModal(false)}>
        <div className="authContainer">
          <h3 className="login-h3" >Nature <i className="fas fa-seedling"></i> Choice</h3>
          <hr className="hr"></hr>
          <p className="login-p">Great to have you back!</p>
          <div className="log-row">


            <div className="rightspace">
              <div className="loginInputContainer">
                {signup && (
                  <MaterialInput
                    type="text"
                    label="First Name"
                    value={firstName}
                    onChange={(e) => setFirstName(e.target.value)}
                  />
                )}
                {signup && (
                  <MaterialInput
                    type="text"
                    label="Last Name"
                    value={lastName}
                    onChange={(e) => setLastName(e.target.value)}
                  />
                )}

                <MaterialInput
                  type="text"
                  style={{ marginBottom: "505px" }}
                  label="Email/Mobile Number"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                />
                <br/>
                <MaterialInput
                  type="password"
                  label="Password"
                  style={{ paddingBottom: "505px" }}

                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                // rightElement={<a href="#">Forgot?</a>}
                />
                <MaterialButton
                  title={signup ? "REGISTER" : "LOG IN"}
                  bgColor="#000"
                  textColor="#ffffff"

                  style={{
                    margin: "40px 0 20px 0",

                  }}
                  onClick={userLogin}
                />

              </div>
            </div>
          </div>
        </div>
      </Modal>
      <div className="subHeader">
        {/* Logo  */}
        {/* <div className="logo"> */}
        {/* <a href="/">
           <img src={flipkartLogo} className="logoimage" alt="" /> 
        </a> */}
        <a >
          {/* <span><a href="/" className="exploreText">Nature</a></span> */}
          <Link to="/" style={{ color: '#518432', textDecoration: 'none', fontSize: "50px" }}>Nature<i className="fas fa-seedling" style={{ color: '#518432' }}></i>Choice</Link>

        </a>
        {/* </div> */}
        {/* logo ends here */}

        {/* search component */}
        <div
          style={{
            padding: "0 7px",
          }}
        >
          {/* <div className="searchInputContainer">
            <input
              className="searchInput"
              placeholder={"search for products, brands and more"}
            />
            <div className="searchIconContainer">
              <IoIosSearch
                style={{
                  color: "#2874f0",
                }}
              />
           </div>
          </div> */}
        </div>

        {/*.............sssssssssss............. */}
        {/* right side menu */}
        <div className="rightMenu">
          {auth.authenticate ? renderLoggedInMenu() : renderNonLoggedInMenu()}


          <span><Link to="/" style={{ color: 'black', textDecoration: 'none', paddingRight: '25px' }}>Home</Link></span>

          <span><Link to="/about" style={{ color: 'black', textDecoration: 'none', paddingRight: '25px' }}>About</Link></span>

          <span><Link to="/gallery" style={{ color: 'black', textDecoration: 'none', paddingRight: '25px' }} >Gallery</Link></span>

          <span><Link to="/ContactUs" style={{ color: 'black', textDecoration: 'none' }} >Contact Us</Link></span>

          <div>
            <Link to="/cart" className="cart" style={{ color: 'black', textDecoration: 'none' }}>
              <Cart count={Object.keys(cart.cartItems).length} />
              {/* <span><Link to="/cart" style={{ color: 'black', textDecoration: 'none' }}>Cart</Link></span> */}
            </Link>
          </div>
        </div>
        {/* right side menu ends here */}


      </div>
    </div>
  );
};

export default Header;
